* {
  margin: 0;
  padding: 0;
}
header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  padding: 5px;
  background-color: #1f1d2b;
  color: white;
}
header nav,
header div {
  flex: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px;
}
header div img {
  display: block;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
}
header nav ul {
  font-size: 20px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
}
header nav ul li a {
  color: whitesmoke;
  text-decoration: underline;
}
a:hover,
a:focus {
  color: #f27d4d;
}

.content {
  display: flex;
  width: 100%;
  background-color: whitesmoke;
  justify-content: center;
  align-items: center;
  min-height: 300px;
}

.content .home {
  background-color: #1f1d2b;
  opacity: 10;
  color: white;
  margin: 50px;
  flex: 50%;
  display: flex;
  min-height: 300px;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.content .home h2 {
  flex: 100%;
  text-align: center;
  font-size: 48px;
}

.content .home button {
  margin: 20px;
  width: max-content;
  padding: 10px;
  cursor: pointer;
  font-size: 30px;
  border: none;
  background-color: #00c0c9;
}
.content .home button:hover {
  background-color: #f27d4d;
}
footer {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: space-around;
  width: 100%;
  margin: auto;
  flex-wrap: wrap;
  background-color: #1f1d2b;
  color: whitesmoke;
}
footer p {
  flex: 100%;
  text-align: center;
  padding: 10px;
}
footer ul {
  flex: 100%;
  list-style-type: none;
  text-align: center;
  padding: 10px;
}
.content .about {
  width: 70%;
  font-size: 24px;
  text-align: justify;
}
